@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  height: 100%;

  background-color: pink;
  font-family: 'Fredoka One', sans-serif;
  position: relative;
}

h1, h2, h3, h4 {
  font-family: 'Fredoka One', sans-serif;
  text-align: center;
}

a{
  color:rgb(32, 116, 185);
  text-decoration: none;
}

a:visited{
  color:rgb(15, 82, 82);
}

a:hover{
  color:rgb(0, 163, 163);
  transition: 0.2s ease;
}


.App{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: table;

}

.main{
  display: table-cell;
  vertical-align: middle;
}

.mainArea{
  margin-left:auto;
  margin-right: auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  text-align: center;
}

#startPart{
  text-align: center;
  font-size: larger;
}

#startPart img{
  width: 250px;
}

.start-btn{
  background-color: purple;
  color:white;
  border: none;
  border-radius: 100px;
  font-size: 45px;
  font-family: inherit;
  padding: 0.25em 0.5em;
  margin:1rem 0;
}

.start-btn:hover{
  background-color: rgb(54, 24, 54);
  color:rgb(184, 184, 184);
}

.chooseStomachSize{
  text-align: center;
}

.chooseStomachSize img{
  width: 200px
}

.btn-grp{
  display: inline;
  margin: 0 auto;
}

.btn-size{
  background-color: rgb(139, 32, 32);
  color:white;
  border: none;
  border-radius: 100px;
  font-size: 35px;
  margin: 0 1rem;
  font-family: inherit;
  padding: 0.25em 0.5em;
}

.btn-size:hover{
  background-color: rgb(90, 14, 14);
  color: rgb(184, 184, 184);
}

#foodtostomach{
  display:flex;
  flex-wrap: wrap;
}

.foodArea{
  display: flex;
  align-items: center;
  justify-content: center;
}

.random-btn{
  height:-webkit-min-content;
  height:-moz-min-content;
  height:min-content;
  background-color: rgb(139, 32, 32);
  color:white;
  border: none;
  border-radius: 100px;
  font-size: 35px;
  margin: 0 1rem;
  font-family: inherit;
  padding: 0.25em 0.5em;
}

.random-btn:hover{
  background-color: rgb(90, 14, 14);
  color: rgb(184, 184, 184);
}

#FoodSelection{
  width:-webkit-min-content;
  width:-moz-min-content;
  width:min-content;
  display:grid;
  grid-template-columns: auto auto;
  justify-content: space-evenly;
}

.food-card{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  
  background-color: white;
  width: 250px;
  padding: 2em;
  border-radius: 75px;
  text-align: center;
  margin:1em;
  height:250px;

  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
     -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
          user-select: none; /* Non-prefixed version, currently
                                supported by Chrome, Edge, Opera and Firefox */
}

.food-card img{
  /* Change so that it changes size to maintain aspect ratio */
  max-width: 200px;
  max-height: 200px;
 white-space: pre;
}

.food-card h2 {
  margin: 0;
}

.stomachList {
  width: 250px;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  margin: auto 1rem;
}

.stomach-foodList{
  border-radius: 2rem;
  /* width: 250px; */
  height: 500px;
  padding: 0.5rem 2em;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: hotpink;
  text-align: left;
}
